/* -------------------------------------------------------------------------- */
/*                                Text styles                               */
/* -------------------------------------------------------------------------- */

@import './bq-text.variables';

:host {
  @apply inline-block;
}

.bq-text,
.bq-text * {
  @apply bg-[color:--bq-text--background-color] font-[--bq-text--font-weight] text-[color:--bq-text--text-color];
  @apply rounded-[--bq-text--border-radius] border-[length:--bq-text--border-width] border-[color:--bq-text--border-color];

  border-style: var(--bq-text--border-style);
  box-shadow: var(--bq-text--box-shadow);
}

.size {
  &--xs * {
    @apply text-[length:var(--bq-text--size-xs)];
  }

  &--s * {
    @apply text-[length:var(--bq-text--size-s)];
  }

  &--m * {
    @apply text-[length:var(--bq-text--size-m)];
  }

  &--l * {
    @apply text-[length:var(--bq-text--size-l)];
  }

  &--xl * {
    @apply text-[length:var(--bq-text--size-xl)];
  }

  &--xxl * {
    @apply text-[length:var(--bq-text--size-xxl)];
  }

  &--xxl2 * {
    @apply text-[length:var(--bq-text--size-xxl2)];
  }

  &--xxl3 * {
    @apply text-[length:var(--bq-text--size-xxl3)];
  }

  &--xxl4 * {
    @apply text-[length:var(--bq-text--size-xxl4)];
  }

  &--xxl5 * {
    @apply text-[length:var(--bq-text--size-xxl5)];
  }
}

.digit {
  @apply min-bs-[--bq-text--size-large] min-is-[--bq-text--size-large];
}
